class Data extends React.Component{
    
    render(){
        return(
            <div className="container">
            <ul>
            <img className="flag-image"
            src={this.props.flag} alt='Flag'
            />
            {this.props.country && <li>Country: {this.props.country}</li>}
            {this.props.population && <li>Population: {this.props.population}</li>}
            {this.props.capital && <li>Capital City: {this.props.capital}</li>}
            {this.props.currency && <li>Currency: {this.props.currency}</li>}
            {this.props.region && <li>Region: {this.props.region}</li>}
            {this.props.error && <li>{this.props.error}</li>}

            </ul>
            <style jsx>{`
            
            .container{
                text-align: center;
                position: relative;
                justify-content:justify;
                border: 1px #333 solid
                
            }
            li{
                color: white;
                list-style: none;
                line-height: 1.5em;
            }
            li:nth-child(odd){
            }
            .flag-image{
                width: 400px;
                height: 300px;
                border-radius: 10px;
            }
            `}</style>
            </div>
        )
    }
}

export default Data;